import React, { useState } from 'react';
import './CustomSelect.css';

const CustomSelect = ({ label, labelSelect, options, className, onChange }) => {
  const [selectedValue, setSelectedValue] = useState('');
  const [isOpen, setIsOpen] = useState(false);

  const handleSelect = (value) => {
    setSelectedValue(value);
    setIsOpen(false);
    onChange(value);
    
  };

  return (
    <div className={`custom-select-container ${className}`}>
      <label>{label}</label>
      <div
        className={`custom-select ${isOpen ? 'open' : ''} ${className}`}
        onClick={() => setIsOpen(!isOpen)}
      >
        <div className="selected-value">{selectedValue || labelSelect}</div>
        <div className={`arrow ${isOpen ? 'open' : ''}`} />
      </div>
      {isOpen && (
        <div className="options-container">
          {options.map((option) => (
            <div
              key={option.value}
              className="option"
              onClick={() => handleSelect(option.label)}
            >
              {option.label}
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default CustomSelect;
